
export const Photos = (state={
    photos:[],
    loading:false,
    errMessage:null
},action) =>{
    switch (action.type){
        case 'ADD_PHOTOS':
            return {...state,photos:action.payload,loading:false,errMessage:null};
        case 'LOADING_PHOTOS':
            return {...state,photos:[],loading:true,errMessage:null};
        case 'ERROR_PHOTOS':
            return {...state,photos:[],loading:false,errMessage:action.payload};
        case 'ADD_PHOTO':
            return {...state,photos:state.photos.concat(action.payload)};
        case 'UPDATE_PHOTO':
            var updated_index = 0;
            for(var i = 0;i< state.photos.length;i++)
            {
                if(state.photos[i].id === action.payload.id)
                {
                    updated_index = i;
                }
            }
            
            Object.assign(state.photos[updated_index],action.payload)
            return {...state,photos:state.photos};
        case 'DELETE':
            var deleted_index = 0;
            for(var j = 0;j< state.photos.length;j++)
            {
                if(state.photos[j].id === action.payload.id)
                {
                    deleted_index = j;
                }
            }
            state.photos.splice(deleted_index,1);
            return {...state,photos:state.photos}
        default:
            return state;
    }
}