import React,{Component} from 'react'
import Header from './Header';
import {fetchPhotos,updatePhoto,uploadPhoto,deletePhoto} from '../redux/ActionCreators';
import {connect} from 'react-redux';
import {Routes,Route} from 'react-router-dom';
import Photos from './photosComponent';
import Favourite from './favouritComponent';

const mapStateToProps = state =>{
    return{
        photos:state.photos
    }
}
const mapDispatchToProps =(dispatch) =>({
    fetchPhotos : () => {dispatch(fetchPhotos())},
    updatePhoto :(photo) => dispatch(updatePhoto(photo)),
    uploadPhoto :(photo) => dispatch(uploadPhoto(photo)),
    deletePhoto :(photo) => dispatch(deletePhoto(photo))
})
class Main extends Component {
    componentDidMount(){
        this.props.fetchPhotos();
    }
    render(){
        return (
            <>
                <Header uploadPhoto = {this.props.uploadPhoto}/>
                
                        <Routes>
                            <Route path='/FavouritesPhotos' element={<Photos photos = {this.props.photos} updatePhoto ={this.props.updatePhoto} deletePhoto ={this.props.deletePhoto}/> } />
                            <Route path='/favourites' element={<Favourite photos = {this.props.photos} updatePhoto ={this.props.updatePhoto} deletePhoto ={this.props.deletePhoto}/>} />
                        </Routes>
                    
            </>
          );
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(Main);
