export const fetchPhotos = () => (dispatch) => {
    dispatch(loadingPhotos());

    return fetch('https://favouritesapp.herokuapp.com/photos')
    .then(response => response.json())
    .then(photos => dispatch(addPhotos(photos)))
    .catch(error=>console.log(error.message))
}

export const loadingPhotos = () =>({
    type:'LOADING_PHOTOS',
})
export const addPhotos =(photos)=>({
    type:'ADD_PHOTOS',
    payload:photos
    
})

export const updatePhoto = (photo) => (dispatch) =>{
    return fetch('https://favouritesapp.herokuapp.com/photos/'+photo.id,{
        method:'PUT',
        headers: { 
            'Content-Type': 'application/json'
        },    
        body:JSON.stringify({
            img_url:photo.img_url,
            favourite:!photo.favourite
        })
    })
    .then(response => response.json())
    .then(response => dispatch(updateState(response)))
    .catch(error => console.log(error.message))
}

export const uploadPhoto = (photo) => (dispatch) =>{
    const formdata = new FormData();
    formdata.append("file",photo);
    formdata.append("upload_preset","hgt4nbf2");

    return fetch('https://api.cloudinary.com/v1_1/dfcjxnzvi/image/upload',{
        method:"POST",
        body:formdata
    })
    .then(response=>response.json())
    .then(response=> dispatch(uploadPhotoToJson(response.secure_url)))
    .catch(error=>console.log(error.message));
}

export const uploadPhotoToJson = (photo) => (dispatch) => {
    return fetch('https://favouritesapp.herokuapp.com/photos',{
        method:"POST",
        headers: { 
            'Content-Type': 'application/json'
        },
        body:JSON.stringify({
            "img_url":photo,
            "favourite":false
        })
    })
    .then(response => response.json())
    .then(response=> dispatch(addPhoto(response)))
    .catch(error => console.log(error.message))
}

export const addPhoto = (photo) => ({
    type:'ADD_PHOTO',
    payload:photo
})

export const updateState = (photo) =>({
    type:'UPDATE_PHOTO',
    payload:photo
})

export const deletePhoto = (photo) => (dispatch) =>{
    return fetch('https://favouritesapp.herokuapp.com/photos/'+photo.id,{
        method:'DELETE',
        headers: { 
            'Content-Type': 'application/json'
        }
    })
    .then(response => response.json())
    .then(response => dispatch(deleteState(photo)))
    .catch(error => console.log(error))
}

export const deleteState = (photo) =>({
    type:'DELETE',
    payload:photo
})