import React,{useState} from 'react'
import {motion} from 'framer-motion'

const Photos =(props) => {
    const [item,setItem] = useState(null)
    const menu = props.photos.photos.map((photo)=>{
        return(
            <div  key={photo.id}>
                <div className='col'>
                    <motion.div animate={{scale:1}}
                                initial={{scale:0}}
                                class="card h-100 position-relative border-0 shadow-lg"
                    >
                        {photo.favourite? 
                            <i onClick={()=>props.updatePhoto(photo)} className='position-absolute fs-4 bi bi-heart-fill text-warning favourite'></i>:
                            <i onClick={()=>props.updatePhoto(photo)} className='position-absolute fs-4 bi bi-heart text-warning favourite'></i>
                        }
                        <i data-bs-toggle="modal" data-bs-target="#staticBackdrop" onClick={()=> setItem(photo)} className='position-absolute left-0 fs-4 bi bi-trash text-white bg-danger px-2 rounded-2 delete'></i>
                        <img src={photo.img_url} class="card-img-top h-100 rounded-2" alt="..." />
                        
                    </motion.div>
                </div>

                <div class="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                    <div class="modal-dialog">
                        <div class="modal-content">
                            <div class="modal-header">
                                <h5 class="modal-title" id="staticBackdropLabel">Modal title</h5>
                                <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                            </div>
                            <div class="modal-body">
                                Are you sure ?
                            </div>
                            <div class="modal-footer">
                                <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                                <button type="button" class="btn btn-danger" data-bs-dismiss="modal" onClick={()=> props.deletePhoto(item)}>delete</button>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        )
    });
    if(props.photos.loading){
        return(
            <div className='container'>
                <div className='row'>
                    <div className='text-center'>
                        <div class="spinner-border" role="status">
                            <span class="visually-hidden">Loading...</span>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
    else{
        return(
            <div className='container'>
                <div class="row row-cols-1 row-cols-md-3 g-4">
                    {menu}
                </div>
                
            </div>
        )
    }
}
export default Photos;