import React,{useState} from 'react';
import {Link} from 'react-router-dom';
import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from "yup";

const schema = yup.object().shape({
    photoUpload: yup.mixed().required().test("type","we only support images ",(value)=>{return value && value[0].type === "image/jpeg" ||value[0].type === "image/png";})
  });
const Header = (props) => {
    const [toggle,setToggle] = useState(false);
    
    const { register, handleSubmit,resetField , formState:{errors} } = useForm({resolver:yupResolver(schema),defaultValues:{photoUpload:''}});
    const onSubmit = data => {
        props.uploadPhoto(data.photoUpload[0]);
        resetField("photoUpload");
    }
    return (
      <div>
          <nav class="navbar navbar-expand-lg bg-light bg-opacity-25">
            <div class="container">
                <a class="navbar-brand" href="#">Navbar</a>
                <button onClick={()=>setToggle(!toggle)} class="navbar-toggler border-0 shadow-none" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span>{toggle?<i className='bi bi-x fs-2'></i>:<i className='bi bi-list fs-2'></i>}</span>
                </button>
                <div class="collapse navbar-collapse" id="navbarNav">
                    <ul class="navbar-nav">
                        <li class="nav-item">
                           <Link to="/FavouritesPhotos" class="nav-link px-lg-3 text-decoration-none fw-bolder" aria-current="page"><i className='bi bi-house-fill pe-2 '></i>Home</Link>
                        </li>
                        <li class="nav-item">
                            <Link to='/favourites' class="nav-link px-lg-3 text-decoration-none fw-bolder"><i className='bi bi-house-heart-fill pe-2'></i>Favourites</Link>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link px-lg-3 text-decoration-none fw-bolder" data-bs-toggle="modal" href="#uploadPhotoModal"><i className='bi bi-cloud-plus-fill pe-2'></i>Add Photo</a>
                        </li>
                    </ul>
                </div>
            </div>
            </nav>

            <div class="modal fade" id="uploadPhotoModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div class="modal-dialog">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title" id="exampleModalLabel">Modal title</h5>
                            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        <div class="modal-body">
                            <form onSubmit={handleSubmit(onSubmit)}>
                                <input {...register("photoUpload")} type='file'/>
                                {errors.photoUpload && <p className='text-danger'><i class="bi bi-exclamation-triangle pe-2"></i>{errors.photoUpload.message}</p>}
                                <br />
                                <br />
                                <input type="submit"/>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
      </div>
    );
  }

export default Header;


